rif;\n  color: white;\n}\n\na {\n  text-decoration: none;\n}\n\n#logo {\n  height: 44px;\n}\n\n.hero-caption, .rest-img > p, .rest-img {\n  display: flex;\n}\n\n.skip-link {\n  display: flex;\n  position: absolute;\n  top: 25px;\n  left: -100vh;\n  background-color: $primary;\n  border-radius: 5px;\n  height: 44px;\n  padding: 0 20px;\n  transition: all 0.3s ease-in-out;\n  justify-content: center;\n  align-items: center;\n  z-index: 101;\n\n  &:focus{\n    left: 10px;\n  }\n}\n\nmain {\n  display: flex;\n  flex-direction: column;\n}\n\nmain.blurred, footer.blurred{\n  filter: blur(5px);\n  transition: all 0.3s ease-in-out;\n}\n\n#hamburger {\n  position: relative;\n  font-size: 24px;\n  min-width: 44px;\n  min-height: 44px;\n  color: white;\n  border: none; \n  background-color: transparent;\n  cursor: pointer;\n  z-index: 99;\n  text-align: right;\n}\n\n.hero-caption, \n.rest-img > p {\n  flex-direction: column;\n}\n\nnav {\n  display: flex;\n  position: fixed;\n  width: 100%;\n  z-index: 10;\n  transition: all 0.3s ease-in-out;\n  .nav-content {\n    display: flex;\n    width: inherit;\n    align-items: center;\n    padding: 20px 15px;\n    justify-content: space-between;\n  }\n}\n\n.nav-list{\n  display: none;\n  flex-direction: column;\n  gap: 10px;\n  justify-content: center;\n  align-items: center;\n  top: 0;\n  right: 0;\n  height: 100vh;\n  width: 60%;\n  margin: auto;\n  background-color: $secondary;\n  animation: fade 0.3s ease-in-out;\n      @keyframes fade {\n        from{\n          opacity: 0;\n          transform: translate(50vw, 0);\n        }\n        to{\n          opacity: 1;\n          transform: translate(0, 0);\n        }\n      }\n      a {\n        padding: 11px 20px;\n        text-align: center;\n        border-radius: 5px;\n        background-color: $primary;\n        transition: background-color 0.3s ease;\n      \n        &:hover {\n          background-color: lighten($primary, 10%);\n        }\n      }      \n}\n\n.nav-list.show{\n  display: flex;\n  position: absolute;\n}\n\n.hero-element {\n  display: flex;\n  position: relative;\n  height: 420px;\n  overflow: hidden;\n}\n\n.darker-img {\n  position: absolute;\n  inset: 0;\n  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.50) 100%);\n}\n\n#hero {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n\n.hero-caption {\n  inset: 0;\n  position: absolute;\n  font-size: 24px; \n  font-weight: 600; \n  padding: 0 15px;\n  margin: auto;\n  word-wrap: break-word;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  .caption {\n    white-space: normal;\n  }\n}\n\n.rest-element {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding: 40px 15px;\n}\n\n.rest-head {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  position: relative;\n  margin-bottom: 40px;\n  text-align: center;\n  h2, p {\n    color: black;\n  }\n  p {\n    font-size: large;\n  }\n}\n\n.rest-list {\n  display: grid;\n  grid-template-columns: repeat(1, minmax(0, 1fr));\n  gap: 28px 20px;\n  margin-bottom: 40px;\n}\n\n.rest-item {\n  max-width: 360px;\n  transition: transform ease 0.3s, filter ease 0.3s;\n  &:hover {\n    transform: translateY(-10px); \n    filter: drop-shadow(0px 5px 15px rgba(0, 0, 0, 0.3));\n  }\n}\n.rest-img, .rest-img > img {\n  border-top-left-radius: 10px;\n  border-top-right-radius: 10px;\n}\n\n.rest-img {\n  position: relative;\n  overflow: hidden;\n  img {\n    object-fit: cover;\n    height: 180px;\n    width: 360px;\n    transition: transform 0.3s ease, filter 0.3s ease;\n    &:hover {\n      transform: scale(1.1);\n      filter: brightness(110%) contrast(110%);\n    }\n  }\n  p {\n    inset: 0;\n    position: absolute;\n    padding: 4px 20px;\n    height: fit-content;\n    width: fit-content;\n    border-top-right-radius: 5px;\n    border-bottom-right-radius: 5px;\n    justify-content: start;\n    margin-top: 20px;\n    background-color: $primary;\n  }\n}\n\n.rest-cont {\n  background-color: $tertiary;\n  padding: 5px 20px 15px 20px;\n  border-bottom-left-radius: 10px;\n  border-bottom-right-radius: 10px;\n  .title {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    font-weight: 600;\n    h3 {\n      a{\n        color: #fff5f1e7;\n        transition: color 0.3s ease;\n        display: -webkit-box;\n          -webkit-line-clamp: 1;\n          -webkit-box-orient: vertical;\n        overflow: hidden;\n        line-height: 46px;\n        max-width: 100%;\n        &:hover{\n          color: white;\n        }\n      } \n    }\n  }\n}\n\n.rating, .desc, .rest-head,\n.rest-img > p, .caption {\n  cursor: default;\n}\n\n.rest-cont > p {\n  font-size: 14px;\n  height: 80px;\n  text-align: justify;\n  word-wrap: break-word;\n  overflow: hidden;\n  display: -webkit-box;\n    -webkit-line-clamp: 4;\n    -webkit-box-orient: vertical;\n}\n\n.loader-wrapper{\n  width: 100%;\n  height: 350px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.display-none {\n  display: none;\n}\n\n.loader {\n  width: 50px;\n  padding: 8px;\n  aspect-ratio: 1;\n  border-radius: 50%;\n  background: $primary;\n  --_m: \n    conic-gradient(#0000 10%,#000),\n    linear-gradient(#000 0 0) content-box;\n  -webkit-mask: var(--_m);\n          mask: var(--_m);\n  -webkit-mask-composite: source-out;\n          mask-composite: subtract;\n  animation: l3 1s infinite linear;\n}\n@keyframes l3 {to{transform: rotate(1turn)}}\n\n.detail-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  padding: 40px 15px;\n  margin-top: 80px;\n  .detail-head {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    margin-bottom: 10px;\n    h1, span, p {\n    color: black;\n    }\n    a {\n      font-weight: bold;\n      color: $primary;\n    }\n    span {\n      font-size: large;\n    }\n    img {\n      margin: 20px 0;\n      width: 100%;\n      height: 400px;  \n      object-fit: cover;\n      border-radius: 10px;\n    }\n    .detail-address, .detail-category {\n      display: flex;\n      flex-direction: row;\n      font-size: large;\n      padding: 2px 0;\n      cursor: default;\n      span {\n        display: flex;\n        justify-content: center;\n        width: 30px;\n        font-weight: bolder;\n        color: $primary;\n      }\n    }\n  }\n  .detail-body, .detail-ulasan {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    p, strong, h2 {\n      color: black;\n    }\n    strong {\n      margin-top: 10px;\n    }\n    .detail-desc {\n      text-align: justify;\n    }\n    h2, h3 {\n      text-align: center;\n    }\n    h3 {\n      padding: 10px 0;\n      margin-top: 20px;\n      border-radius: 5px;\n      background-color: $primary;\n    }\n    .detail-menu {\n      display: flex;\n      flex-flow: column;\n      margin-bottom: 40px;\n      .detail-menu-container{\n        .detail-menu-item{\n          .detail-menu-name{\n            padding: 10px;\n            border-bottom: 1px solid $secondary;\n            span {\n              color: $primary;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n.divider {\n  display: flex;\n  width: 100%;\n  height: 1px;\n  background: black;\n  margin: 20px 0;\n}\n\n.foot-co {\n  display: inline-flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  width: 100%;\n  margin: auto;\n  background-color: $secondary;\n  padding: 20px 0 30px 0;\n  gap: 10px;\n  img {\n    height: 50px;\n    width: 200px;\n  }\n  p > a:hover{\n    color: gold;\n  }\n}","@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap');\r\n\r\n// color\r\n$primary: #9D121B;\r\n$secondary: #222222;\r\n$tertiary: #57423F;\r\n\r\n// width\r\n$full: 1064px;"],"sourceRoot":""}]);
